type MyState = {
  showModal: boolean;
  activeProject: number;
};

const projectArray = [
  gameOmen,
  gameRevolution,
  gamePop,
  gameViva,
  webGinevitalis,
  webConduse,
  webBelab,
  webSasi,
  webVazquez,
];

class Experience extends React.Component<any, MyState> {
  constructor(props) {
    super(props);
    this.toggleModal = this.toggleModal.bind(this);
    this.state = {
      showModal: false,
      activeProject: 0,
    };
  }

  toggleModal(id: number) {
    let newState = !this.state.showModal;
    this.setState({
      showModal: newState,
      activeProject: id,
    });
  }

  render() {
    return (
      <section id="experience" className={Style.experience}>
        <h2 className={sectionHeader}>Video Game Background</h2>
        <div className={Style.cardsContainer}>
          <Card project={gameOmen} onClick={() => this.toggleModal(0)} />
          <Card project={gameRevolution} onClick={() => this.toggleModal(1)} />
          <Card project={gamePop} onClick={() => this.toggleModal(2)} />
          <Card project={gameViva} onClick={() => this.toggleModal(3)} />
        </div>
        <br />
        <h2 className={sectionHeader} style={{ marginTop: "50px" }}>
          Web Development Background
        </h2>
        <div className={Style.cardsContainer}>
          <Card project={webGinevitalis} onClick={() => this.toggleModal(4)} />
          <Card project={webConduse} onClick={() => this.toggleModal(5)} />
          <Card project={webBelab} onClick={() => this.toggleModal(6)} />
          <Card project={webSasi} onClick={() => this.toggleModal(7)} />
          <Card project={webVazquez} onClick={() => this.toggleModal(8)} />
        </div>
        {/* MODAL */}
        <Modal
          url={projectArray[this.state.activeProject].projectURL}
          title={projectArray[this.state.activeProject].title}
          onClose={() => this.toggleModal(this.state.activeProject)}
          show={this.state.showModal}
        >
          {projectArray[this.state.activeProject].fullDesc}
          {projectArray[this.state.activeProject].imgShow}
        </Modal>
      </section>
    );
  }
}

export default Experience;
