import React from "react";
import {
  card,
  cardButton,
  cardTitle,
  column,
  padding,
  row,
} from "./style.module.css";

import {Belab, Conduse, Ginevitalis, NextRevolution, Omen, PopFly, Sasi, Vazquez, VivaSanchoVilla} from "./images";

import { Project } from "./Project";

interface Dictionary<T> {
  [Key: string]: T;
}

const Card = (props: { project: Project, onClick: any}) => {

    let img: Dictionary<any> = {
        "OMEN": <Omen />,
        "NEXT REVOLUTION": <NextRevolution />,
        "POP FLY": <PopFly />,
        "VIVA SANCHO VILLA": <VivaSanchoVilla />,
        "GINEVITALIS": <Ginevitalis />,
        "CONDUSE": <Conduse />,
        "BELAB": <Belab />,
        "SASI": <Sasi />,
        "VAZQUEZ": <Vazquez />
    };

  return (
    <div className={card}>
      <div className={row}>
        <div className={column}>
            {img[props.project.imgURL]}
        </div>
        <div className={column}>
          <div className={padding}>
            <h5 className={cardTitle}>{props.project.title}</h5>
            <div style={{ fontFamily: "monospace" }}>
              {props.project.smallDesc}
            </div>
            <nav>
              <button className={cardButton} onClick={props.onClick}>More Info</button>
            </nav>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Card;
